@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans&family=Varela+Round&display=swap);
* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

a {
  text-decoration: none;
  color: lightgray;
}

body {
  font-family: 'Montserrat', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #282c43;
  color: white;
  max-width: 1350px;
  margin: 0 auto;

}



.App{padding:20px;text-align:left}.won-card{background-color:#59B65A}.lost-card{background-color:#B63131}

.MatchDetailCard{display:grid;grid-template-columns:1fr 1fr;padding:10px;margin-bottom:20px}.MatchDetailCard .match-date,.MatchDetailCard .match-venue,.MatchDetailCard .match-result{margin-top:10px}.MatchDetailCard .innings-detail{text-align:right}.MatchDetailCard .innings-detail>p{margin-bottom:20px}

.MatchSmallCard{padding:10px;margin-bottom:20px}.MatchSmallCard .match-result{margin-top:10px}.MatchSmallCard .innings-detail{text-align:right}.MatchSmallCard .innings-detail>p{margin-bottom:20px}

.TeamPage{display:grid;grid-template-columns:1fr 1fr 1fr 1fr;grid-template-rows:1fr 1fr 1fr;grid-column-gap:10px;grid-row-gap:10px}.TeamPage .team-name-section{grid-column:span 3;justify-self:start;align-self:center}.TeamPage .team-name{font-size:3rem}.TeamPage .win-loss-section{justify-self:center;padding:20px;text-align:center}.TeamPage .match-detail{grid-column:span 4}.TeamPage .more-link{-webkit-align-self:center;align-self:center;justify-self:center;font-size:1.5rem}

.YearSelector{text-align:right;list-style-type:none}.YearSelector li{margin-top:5px;margin-bottom:5px}

.MatchPage{display:grid;grid-template-columns:1fr 6fr;grid-column-gap:10px}.MatchPage .year-selector{justify-self:end;padding:20px}.MatchPage .page-heading{margin:20px, 20px}

.HomePage .app-name{margin:30px auto}.HomePage .team-grid{display:grid;grid-template-columns:1fr 1fr;grid-column-gap:20px;-webkit-column-gap:20px;column-gap:20px;grid-row-gap:20px;row-gap:20px}

.team-tile{height:80px;text-align:center;font-family:'Open Sans', sans-serif;border:1px solid white;padding:20px}

